@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff"); }

body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  background-color: #ffffff;
  color: #000000;
  display: grid;
  grid-template-rows: min-content 1fr min-content; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.overflow-hidden {
  overflow: hidden; }

.container {
  max-width: 1610px;
  padding: 0 130px;
  margin: 0 auto; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.header__question-line {
  background-color: #272222; }

.nav {
  display: flex;
  justify-content: flex-start; }

.nav__burger-menu {
  padding-top: 31px; }

.nav__main-nav {
  padding-left: 40px; }

.burger-menu--closed .burger-menu__list {
  display: none; }

.burger-menu__toggle {
  background-color: #ffffff;
  border: none;
  background-image: url("../img/icon-burger.svg");
  background-size: auto 100%;
  width: 23px;
  height: 16px; }
  .burger-menu__toggle:hover, .burger-menu__toggle:focus {
    cursor: pointer; }

.tel__link {
  color: #000000;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  margin-right: 5px; }
  .tel__link:hover, .tel__link:focus {
    color: #ce3730; }
  .tel__link:active {
    opacity: 0.7; }

.tel__info {
  color: #000000;
  opacity: 0.3;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 100%; }

.question-line {
  padding: 9px 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-end; }

.question-line__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: flex-end; }

.question-line__link {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  margin-left: 22px;
  color: #ffffff;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 140%;
  position: relative;
  transition: 0.2s; }
  .question-line__link:hover, .question-line__link:focus {
    color: #ce3730; }
  .question-line__link:active {
    opacity: 0.5; }

.question-line__link svg {
  stroke: #ffffff; }

.question-line__link:hover svg {
  stroke: #ce3730; }

.question-line__icon-question {
  margin-right: 5px; }

.question-line__icon-check {
  margin-right: 9px; }

.main-nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-left: 40px; }

.main-nav__tel {
  padding-top: 31px; }

.main-nav__inner {
  width: 923px;
  padding-top: 16px;
  padding-bottom: 29px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15); }

.site-nav {
  padding: 0;
  margin: 0;
  list-style: none;
  padding-top: 16px;
  display: flex;
  justify-content: space-between;
  width: 552px; }

.site-nav__link {
  color: #000000;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 100%; }
  .site-nav__link:hover, .site-nav__link:focus {
    color: #ce3730; }
  .site-nav__link:active {
    opacity: 0.7; }
  .site-nav__link--selection {
    position: relative; }
    .site-nav__link--selection::after {
      position: absolute;
      content: "";
      top: 6px;
      right: -10px;
      width: 6px;
      height: 6px;
      background-image: url("../img/icon-arrow-down.svg");
      background-position: center;
      background-repeat: no-repeat; }

.controls-list {
  padding: 0;
  margin: 0;
  list-style: none;
  width: 236px;
  display: flex;
  justify-content: flex-start; }

.control-list__item {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px; }

.controls-list__link {
  color: #000000;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.2s; }
  .controls-list__link:hover, .controls-list__link:focus {
    color: #ce3730; }
  .controls-list__link--geo svg {
    stroke: #000000; }
  .controls-list__link--geo:hover svg {
    stroke: #ce3730; }

.controls-list__icon-wrapper {
  width: 46px;
  height: 46px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.2s; }
  .controls-list__icon-wrapper:hover, .controls-list__icon-wrapper:focus {
    background-color: #ce3730; }

.controls-list__icon-wrapper svg {
  fill: #000000; }

.controls-list__icon-wrapper:hover svg {
  fill: #ffffff; }
